import React, { useState } from 'react'
import { Form, Input, Button } from 'antd';
import { useHistory, NavLink } from "react-router-dom";
import { UserOutlined, LockOutlined, MailOutlined, EyeInvisibleOutlined, EyeTwoTone, UserAddOutlined, PoweroffOutlined } from '@ant-design/icons';
import logo from '../../../assets/img/logofinaln.png'
import './Register.css'
import Zoom from 'react-reveal/Zoom';
import AdminService from '../../../services/admin-Service'
import Notification from '../../shared/notification/Notification'
import { mailTalanValidator } from '../../../constant/Talan-mail-constant'

export default function Register() {
    const history = useHistory();
    const [form] = Form.useForm();
    const [loading, setLoading] = useState(false);
    const [userLoginExist, setUserLoginExist] = useState(false);

    const capitalizeFirstLetter = (string) => {
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    const onFinish = values => {

        setLoading(true)
        const newUser = {
            email: values.email.trim(),
            firstName: capitalizeFirstLetter(values.firstName).trim(),
            lastName: capitalizeFirstLetter(values.lastName).trim(),
            password: values.password,
            login: values.login.toLowerCase().trim()
        }
        AdminService.register(newUser).then(resp => {
            console.log(resp.data)
            setLoading(false)
            history.push({ pathname: "/login" })
            Notification('success', 'Inscription réussie', "Merci de compléter vos informations et valider votre demande d’inscription.")

        }

        ).catch(err => {
            Notification('error', "Erreur d'Inscription", 'Merci de réessayer ')
            setLoading(false)
        })

    }
 
    const IsUserLoginExist = (rule, value, callback) => {

        if (value) {

                AdminService.getUserByLogin(value.toLowerCase().trim()).then(resp => {
                    if (resp.data) {
                    callback("Identifiant existe déjà ");
                    }
                    callback();

                })
                    .catch(err => {
                        console.log(err)
                    })
            }

            
        

        else callback();
    }

    const IsUserEmailExist = (rule, value, callback) => {

        if (value) {

            if (value.toLowerCase().endsWith(mailTalanValidator.toLowerCase())) {
                AdminService.getUserByEmail(value.toLowerCase().trim()).then(resp => {
                    if (resp.data) {
                    callback("Email existe déjà ");
                    }
                    callback();
                })
                    .catch(err => { console.log(err)
                    })
            }

            else callback("Merci d'ajouter un mail Talan");
        }

        else callback();
    }

    return (
        <Zoom >

            <Form form={form} name="normal_register" className="register-form" onFinish={onFinish}>
                <img className="logo" src={logo} alt=".logo" />
                <Form.Item
                    name="firstName"
                    rules={[{
                        required: true,
                        message: 'Quel est votre Nom ?',

                    }
                    ]}
                >
                    <Input prefix={<i className="far fa-address-card" ></i>} placeholder="Nom " />
                </Form.Item>
                <Form.Item
                    name="lastName"
                    rules={[{ required: true, message: 'Quel est votre Prénom ?' }]}
                >
                    <Input prefix={<i className="far fa-address-card" ></i>} placeholder="Prénom" />
                </Form.Item>
                <Form.Item
                    name="email"
                    rules={[{ required: true, message: 'Quel est votre Email ?' }
                        ,
                     {
                         validator: IsUserEmailExist,

                 }
                    ]}
                >
                    <Input prefix={<MailOutlined className="site-form-item-icon" />} placeholder="Email" />
                </Form.Item>
                <Form.Item
                    name="login"
                    rules={[

                        {
                            required: true,
                            message: 'Quel est votre Login ?',
                         },
                        {
                             validator: IsUserLoginExist,

                        }


                    ]}
                >
                    <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Login" />
                </Form.Item>
                <Form.Item
                    name="password"
                    rules={[{ required: true, message: "merci d'ajouter un mot de passe !" }]}
                >
                    <Input.Password
                        prefix={<LockOutlined className="site-form-item-icon" />}
                        type="password"
                        placeholder="Mot de passe"
                        iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                    />
                </Form.Item>
                <Form.Item
                    name="confirm"
                    dependencies={['password']}
                    hasFeedback
                    rules={[
                        {
                            required: true,
                            message: "Merci de confirmer votre mot de passe",
                        },
                        ({ getFieldValue }) => ({
                            validator(_, value) {
                                if (!value || getFieldValue('password') === value) {
                                    return Promise.resolve();
                                }
                                return Promise.reject('Vérifier si les mots de passes sont identiques');
                            },
                        }),
                    ]}
                >
                    <Input.Password
                        prefix={<LockOutlined className="site-form-item-icon" />}
                        type="password"
                        placeholder="confirmer Mot De Passe"
                        iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                    />
                </Form.Item>
                <div className="login-register-container">
                    <Form.Item shouldUpdate={true} className="register_submit_button">
                        {() => (
                            <Button
                                type="primary"
                                htmlType="submit"
                                loading={loading}
                                icon={<UserAddOutlined />}

                            >
                                S'inscrire
                            </Button>
                        )}
                    </Form.Item>
                    <span className="register">  <NavLink to="/login"  > Vous avez déjà un compte ? Connectez-vous </NavLink> </span>
                </div>
            </Form>
        </Zoom>
    )
}
