import React, { Component, useContext, useState, useEffect } from 'react'

// react-bootstrap components
import {Avatar, Checkbox, InputNumber, Modal, Radio, Popconfirm, message, Tooltip, Switch} from 'antd';
import { Form, Card, Input, Button,Select } from 'antd';
import {CloseOutlined, ExclamationCircleOutlined} from '@ant-design/icons';
import './UserProfile.css'
import {
  Container,
  Row,
  Col,
} from "react-bootstrap";
import { UserOutlined, BankOutlined, LockOutlined, EyeInvisibleOutlined, EyeTwoTone, SolutionOutlined, LoadingOutlined, SmileOutlined  } from '@ant-design/icons';
import AdminService from '../../../services/admin-Service'
import ClientService from '../../../services/client-service'
import AuthentificationContext from '../../../context/authentification-context'
import Notification from '../../shared/notification/Notification'
import { Steps } from 'antd';
import { Link } from 'react-router-dom';


const { Step } = Steps;
const { Meta } = Card;
const { TextArea } = Input;

function User() {
  const authentificationContext = useContext(AuthentificationContext);
  const [userDelegate, setUserDelegate] = useState("");
  const [client, setClient] = useState("");
  const children = [];
  const [extentionFile, setExtentionFile] = useState([]);
  const [hasApplication, setHasApplication] = useState(false);
  const [form] = Form.useForm();
  const [formEditted] = Form.useForm();
  const [loading, setLoading] = useState(false);
  const [isModalMotifVisible, setIsModalMotifVisible] = useState(false);
  const [addNewParentFolder, setAddNewParentFolder] = useState(false);
  const { Option } = Select;
  const { confirm } = Modal;
  const [displayListExtention, setDisplayListExtention] = useState([]);
  var listExtentionFile=[];
  const [listParentFolderByClient, setListParentFolderByClient] = useState([]);
  const [value, setValue] = useState(1);
  const [acceptMultipleFile1, setAcceptMultipleFile1] = useState(false);
  const [delegate,setDelegate]=useState("");
  const [parent,setParent]=useState("");
  const [edit,setEdit]=useState(false);
  const [parentExist,setParentExist]=useState(false);
  const [parentInputEmpty,setParentInputEmpty]=useState(false);

  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };
  const getUserDelegate = (login) => {
    login = authentificationContext.profile.login
    AdminService.retrieveUserByLogin(login).then(resp => {
      setUserDelegate(resp.data)
    })
      .catch(err => {
        console.log(err)
      })
  };

  const getExtentionFile = () => {
    ClientService.getAllExtentionFile().then(resp => {
      resp.data.map(item=>{
        children.push(<Option key={item.id}>{item.label}</Option>);
      })
      setExtentionFile(children)
    })
        .catch(err => {
          console.log(err)
        })
  };

  const getParentFolderByClientId = () => {
    ClientService.findParentFolderByClientId(client.id).then(response => {
      setListParentFolderByClient(response.data)
    })
  }






  const userHasApplication = (login) => {
    AdminService.hasApplication(login).then(resp => {
      setHasApplication(resp.data)
      if (resp.data) {
        AdminService.getClient(login).then(resp => {
          setClient(resp.data)
          AdminService.getUser(resp.data).then(response => {
            setDelegate(response.data)
          })
          ClientService.getExtentionFileByClientId(resp.data.id).then(resp => {
            listExtentionFile=resp.data.map(item=>item.extentionFile.label)
            setDisplayListExtention(listExtentionFile)
          })
              .catch(err => {
                console.log(err)
              })

        })
          .catch(err => {
            console.log(err)
          })
      };

    })
      .catch(err => {
        console.log(err)
      })
  };

  const onFinish = values => {
    if(client){
      AdminService.deleteClient(client.id).then(resp => {
      }).catch(err => {
        console.log(err)
      })
    }
    setLoading(true)
    const newClient = {

      application: values.application.trim(),
      login: values.login.trim(),
      password: values.password.trim(),
      totalStorageSize: values.totalStorageSize,
      fileSize: values.fileSize,
      subscribtionDate: new Date(),
      totalUsedStorage:0,
      activated:false,
      email: userDelegate.email,
      description : values.description
    }
    AdminService.validate(userDelegate.id, newClient).then(resp => {
      setLoading(false)
      setHasApplication(true)
      setClient(resp.data)
          //history.push({ pathname: "/login" })
          ClientService.saveExtentionFileByClientId(resp.data.id,listExtentionFile).then(resp => {
            Notification('success', 'Inscription réussie', "Merci d'attendre la validation de l'administrateur!")
          })

      // history.push({ pathname: "/login" })

    }

    ).catch(err => {
      setLoading(false)
      Notification('error', "Erreur d'Inscription", 'Merci de réessayer ')

    })

  }

  function handleChange(value) {
    listExtentionFile=value
  }

  function showRejectModal(value) {
    setIsModalMotifVisible(true);
  }

  function rejectNewStorage(client){
    AdminService.deleteClient(client.id).then(resp => {
      setClient("")
      setHasApplication(false)
      listExtentionFile=[]
    }).catch(err => {
      console.log(err)
    })
  }

  function acceptNewStorage() {
    confirm({
      title: 'Confirmation',
      icon: <ExclamationCircleOutlined />,
      content: 'Si vous cliquez sur le bouton OK votre espace de stockage va avoir la valeur de '+client.newTotalUsedStorage+' Go',
      onOk() {
          AdminService.updateNewStorageForClient(client).then(resp => {
            setClient(resp.data)
            Modal.success({
              content: 'Félicitation votre compte à été validé avec succès désormais vous pouvez créé vos dossier parent pour le téléchargement de vos fichiers',
            });
          })
      },
      onCancel() {
        console.log('Cancel');
      },
    });
  }

  const handleCancel = () => {
    setIsModalMotifVisible(false);
  };


  const addParentFolder = () =>{
    setAddNewParentFolder(true)
  }

  const closeInput= () =>{
    setAddNewParentFolder(false)
    setParentInputEmpty(false)
    setParentExist(false)
  }

  function confirmDelete(parentFolder) {
    ClientService.desactivateParentFolder(parentFolder).then(response=>{
      Notification('success', 'Dossier parent désactivé avec succés')
      getParentFolderByClientId()
    })

  }

  const saveParentFolder = () => {
    setParentInputEmpty(false)
    if (parent != '') {
      var listParents = []
      listParentFolderByClient.forEach(item => listParents.push(item.paramParentFolderCompositeID.parent))
      if (!listParents.includes(parent)) {
        ClientService.saveNewParentFolder({
          "paramParentFolderCompositeID": {
            "parent": parent,
            "owner": delegate.id
          },
          "active": true,
          "disabled": true,
          "client": client,
          "acceptMultipleFile": acceptMultipleFile1
        }).then(resp => {
          Notification('success', 'Dossier parent ajouté avec succés')
          listParentFolderByClient.push(resp.data)
        }).catch(err => {
          console.log(err)
        }).then(() => {
          setAddNewParentFolder(false)
          setParent('')
        })
      }
    }else{
      setParentInputEmpty(true)
    }
  }

  const editParentFolder= (param) =>{
    setEdit(true)
    param.disabled=false
  }

  const saveParentFolderEddited = (param,acceptMultipleFile1,parent)=>{
    if(parent==''){
      parent=param.paramParentFolderCompositeID.parent
    }
    param.disabled=true
    ClientService.editParentFolder(param,parent,acceptMultipleFile1).then((response)=>{
      Notification('success', 'Dossier parent modifié avec succés')
      setEdit(false)
      getParentFolderByClientId()
    })
  }

  const closeEdit=(param)=>{
    param.disabled=true
    setEdit(false)
  }

  const reactivateParentFolder=(param)=>{
    ClientService.reactivateParentFolder(param).then(response=>{
      Notification('success', 'Dossier parent réactivé avec succés')
      getParentFolderByClientId()
    })
  }

  const deleteParentFolder= (param) => {
    ClientService.getDocumentListByParentFolderAndUser(param.paramParentFolderCompositeID.parent,client.login).then(response => {
      if(response.data){
        Modal.confirm({
          title: 'Attention',
          icon: <ExclamationCircleOutlined />,
          content: 'Ce dossier parent ne peut pas être supprimé car il contient des fichiers. Voulez vous le désactiver ?',
          okText: 'Oui',
          cancelText: 'Non',
          onOk:()=>confirmDelete(param),

        });
      }else {
        ClientService.deleteParentFolderByParamFolderCompositId(param.paramParentFolderCompositeID).then(response => {
          Notification('success', 'Dossier parent supprimé avec succés')
          getParentFolderByClientId()
        })

      }
    })

  }


  const onChange = e => {
    if(e.target.value==1){
      setAcceptMultipleFile1(false)
    }else{
      setAcceptMultipleFile1(true)
    }
    setValue(e.target.value);
  };

  const onChangeRadio = e => {
    if(e.target.value==false){
      setAcceptMultipleFile1(false)
    }else{
      setAcceptMultipleFile1(true)
    }
    // setValue(e.target.value);
  }

  const onChangeInput = e => {
    setParent(e.target.value)
    if(e.target.value===''){
      setParentInputEmpty(true)
    }else{
      setParentInputEmpty(false)
      var listParents = []
      listParentFolderByClient.forEach(item => listParents.push(item.paramParentFolderCompositeID.parent))
      if (listParents.includes(e.target.value)) {
        setParentExist(true)
      }else{setParentExist(false)}
    }
  }

  useEffect(() => {

    getExtentionFile()
    getParentFolderByClientId()

  }, [client.id])

  useEffect(() => {

    getUserDelegate(authentificationContext.profile.login)

  }, [authentificationContext.profile.login])
  useEffect(() => {

    userHasApplication(authentificationContext.profile.login)

  }, [])

  return (
    <>
      <Container fluid className="card-Container">

        <Row>
          <Col md="12">

            {client.newTotalUsedStorage==null?hasApplication ? client.activated ?  null :(<Card className="alerteVal"  >
                    <div className="alerteDivVal" >
                      <i class="fas fa-hourglass-half"></i>                <div >
                      <p className="Phrase1Val">
                        Demande d’inscription en attente de validation par l'administrateur !         </p>

                    </div>
                    </div>   </Card>) :
                  (<Card className="alerte"  > <div className="alerteDiv" >
                    <i class="fas fa-exclamation-circle"></i>
                    <div >
                      <p className="Phrase1">
                        Vous devez valider la demande d'inscription de votre application pour acceder aux autres composants  !
                      </p>
                      <p className="Phrase2">
                        Veuillez compléter vos informations et valider la demande d’inscription !        </p>
                    </div>
                  </div>   </Card>):client.newTotalUsedStorage==0?(<Card className="alerteVal"  >
              <div className="alerteDivVal" >
                <i class="fas fa-hourglass-half"></i>                <div >
                <span className="Phrase1Val">
                  Votre demande a été rejetée par l'administrateur, veuillez cliquer <span className="RejectMotif" onClick={()=>showRejectModal(client)}>ici</span> pour voir le motif  </span>

              </div>
              </div>   </Card>):(<Card className="alerteVal"  >
              <div className='row'>
                <div className="col-md-10 alerteDivVal">
                  <i class="fas fa-hourglass-half"></i>
                  <div>
                <span className="Phrase1Val">
                  Votre demande a été rejetée par l'administrateur cause de l'espace de stockage très élevé et pour cela il vous propose {client.newTotalUsedStorage} Go comme espace de stockage. Acceptes tu cette offre ?</span>
                  </div>

                </div>
                <div className="col-md-2 buttonMargin">
                  <Button key="back" className="btn-sm reject-btn " onClick={()=>rejectNewStorage(client)}>Rejeter</Button>&nbsp;
                  <Button key="submit" type="btn-sm accept-btn " onClick={()=>acceptNewStorage(client)}>Accepter </Button>
                </div>
              </div>
              </Card>)}

          </Col>
        </Row>

        {/****************STEP STATUS CLIENT**********/}

        <Row>
          <Col md="2"></Col>
          <Col md="8">
            {client.refuseDesc==null?<Steps>
            <Step status="finish" title="Inscription" icon={<UserOutlined />} />
            {hasApplication==true?<Step status="finish" title="Verification" icon={<SolutionOutlined />} /> :<Step status="wait" title="Verification" icon={<SolutionOutlined />} />}
            {client.refuseDesc!=null?
                <Step status="error" title="Rejet" icon={<CloseOutlined />} />:<div></div>}
            {client.activated==true ?
                <Step status="finish" title="Validation" icon={<SmileOutlined />} />: <Step status="wait" title="Validation" icon={<SmileOutlined />} />}
          </Steps>:<Steps>
                  <Step status="finish" title="Inscription" icon={<UserOutlined />} />
                  <Step status="finish" title="Verification" icon={<SolutionOutlined />} />
                  <Step status="error" title="Rejet" icon={<CloseOutlined />} />
                </Steps>}
        </Col>
          <Col md="2"></Col>
        </Row>

        {/****************STEP STATUS CLIENT**********/}


        <br/>

        <Modal title="Motif du Rejet" visible={isModalMotifVisible}  onCancel={handleCancel}
          footer={[
          <Button hidden={true} key="back" className="back-btn" onClick={handleCancel}>
            Annuler
          </Button>,
        ]}>
          <p>{client.refuseDesc}</p>
        </Modal>


        <Row className="row-Container">
        <Form form={form} className="userProfile-form"
                  name="basic"
                  initialValues={{ remember: true }}
                  onFinish={onFinish}
                  onFinishFailed={onFinishFailed}
                >
          <Col md="8">

            <Card title="Description" bordered={false}  >
            {hasApplication ?
              <TextArea rows={4} showCount maxLength={100} placeholder={client.description}  disabled />
            :    <Form.Item  name="description"
                  rules={[{ required: true, message: 'merci d\'ajouter une description de votre application !' }]}
                >

<TextArea rows={4} showCount maxLength={100} placeholder="Veuillez saisir une brève description de votre application…" />
</Form.Item>
            }
            </Card>
            <Card className="card-Rep" title="Représentant" bordered={false}>
              <div className="card-image">
                <div className="avatar" >
                  <Meta
                    avatar={
                      <Avatar
                        icon="user"
                        shape="circle"
                        size={60}

                      />
                    }
                    title={userDelegate.firstName + " " + userDelegate.lastName}
                  />
                </div>
                <hr></hr>
                <div className="infoClient">
                  <div className="items">
                    <i class="fas fa-user"></i>
                    {userDelegate.login}
                  </div>
                  <div className="items">
                    <i class="fas fa-envelope"> </i> {userDelegate.email}
                  </div>
                  <div className="items">
                    <i class="fas fa-calendar-alt">  </i> 27-04-2021
                                </div>

                </div>
              </div>

            </Card>

            {/**************************PARENT FOLDER COMPONENT******************/}

            {client.activated ? <Card title="Creation Dossier Parent " bordered={false}>
              < i className="fas fa-plus-circle pull-right topbutton cursorPointer" onClick={addParentFolder}> </i>


              {listParentFolderByClient.map((el, key) => <card parentFolder={el} key={key}>
                {edit && !el.disabled ?
                    <Form form={formEditted} name="basic">
                      <div className='row'>

                        <Form.Item className='col-md-5' name="parent">
                          <Input className='parent-folder-name' defaultValue={el.paramParentFolderCompositeID.parent} onChange={onChangeInput}
                          /></Form.Item>

                        <Form.Item className='col-md-5 ' style={{marginLeft: 15}}>
                          <Radio.Group defaultValue={el.acceptMultipleFile} onChange={onChangeRadio}>
                            <Radio value={!value}>Unique</Radio>
                            <Radio value={!(!value)}>Multiple</Radio>
                          </Radio.Group>
                        </Form.Item>

                        <Form.Item>
                          <div style={{marginLeft: -15, position: "relative",}}>
                            <i style={{marginTop: '6px'}} className="fas fa-check-circle cursorPointer colorIcone" onClick={() => saveParentFolderEddited(el,acceptMultipleFile1,parent)}> </i>
                            <i style={{marginTop: '6px'}} className="fa fa-times-circle cursorPointer colorIcone" onClick={() => closeEdit(el)}> </i>
                          </div>
                        </Form.Item>


                      </div>
                    </Form>
                    :
                    <div className='row'>
                      <Input className='col-md-5 parent-folder-name ' value={el.paramParentFolderCompositeID.parent}
                             disabled={el.disabled}/>
                      <div className='col-md-5 '>
                        <Radio.Group value={el.acceptMultipleFile} disabled={el.disabled}>
                          <Radio value={!value}>Unique</Radio>
                          <Radio value={!(!value)}>Multiple</Radio>
                        </Radio.Group>
                      </div>
                      {!edit&&el.active?<i style={{marginTop: '6px'}} className="fa fa-edit cursorPointer colorIcone"
                                onClick={() => editParentFolder(el)}> </i>:''}
                      {!edit&&el.active?<i style={{marginTop: '6px'}} className="fa fa-trash cursorPointer colorIcone"
                         onClick={() => deleteParentFolder(el)}> </i>:''}
                         <i style={{marginTop: '6px'}} className="fa fa-eye cursorPointer colorIcone" 
                         > </i>
                          <Link to="/trace">
     <button type="button">
          Click Me!
     </button>
 </Link>

                      {!el.active?<div>

                        <Tooltip placement="top" title='Ce dossier parent est désactivé'>
                          <i style={{marginTop: '6px'}}   className="fa fa-info-circle colorIconeInfo"> </i>
                        </Tooltip>
                        <Tooltip placement="top" title='réactiver'>
                          <Switch style={{marginTop: '-2px'}} onClick={()=> reactivateParentFolder(el)} size="small" defaultChecked={false} />
                        </Tooltip>

                         </div>:''}

                    </div>}
                <br/>
              </card>)}


              {addNewParentFolder == true ?

                  <card>

                    <div className='row'>
                      <Input className='col-md-5 parent-folder-name' placeholder='Nom du dossier parent'
                             onChange={onChangeInput}/>
                      <div className='col-md-5'>
                        <Radio.Group onChange={onChange} value={value}>
                          <Radio value={1}>Unique</Radio>
                          <Radio value={2}>Multiple</Radio>
                        </Radio.Group>
                      </div>
                      <i style={{marginTop: '6px'}} className="fas fa-check-circle cursorPointer colorIcone"
                         onClick={saveParentFolder}> </i>
                      <i style={{marginTop: '6px'}} className="fa fa-times-circle cursorPointer colorIcone"
                         onClick={closeInput}> </i>
                     
                    </div>
                    {parentExist?<span className="exit">Ce dossier parent existe deja</span>:''}
                    {parentInputEmpty?<span className="exit">Ce champ est obligatoire</span>:''}
                  </card>

                  : ''}
            </Card> : ''}

           {/**************************PARENT FOLDER COMPONENT******************/}

          </Col>
          <Col md="4">
            <Card className="card-APPL">
              <div className="card-image">

                <div className="author">
                  <Avatar icon={<BankOutlined />} shape="circle" size={120} />
                  {hasApplication ? null : <p className="description"> {
                    <div>
                      <div className="addPhotoButtonDiv">
                        <Button
                          shape="round"
                          className="addPhotoButton"
                        >
                          <i className=" far fa-image "></i>
                        </Button>
                      </div>

                    </div>
                  }</p>}

                </div>
              </div>
              <div >
                {(hasApplication&&client.refuseDesc==null)||(hasApplication&&client.newTotalUsedStorage!=0) ? (<div className="FormAppSubsc"
 
                >
                  <Form.Item
                    name="application"
                    rules={[{ required: true, message: 'merci d\'ajouter le nom de l\'application !' }]}
                  >
                    <Input prefix={<i className="far fa-building" ></i>} placeholder={client.application} disabled />
                  </Form.Item>
                  <Form.Item
                    name="login"
                    rules={[{ required: true, message: 'merci d\'ajouter un login !' }]}
                  >
                    <Input prefix={<UserOutlined className="site-form-item-icon" />} disabled placeholder={client.login} />
                  </Form.Item>


                  <Form.Item
                    name="password"
                    rules={[{ required: true, message: 'merci d\'ajouter un mot de passe !' }]}
                  >
                    <Input.Password prefix={<LockOutlined className="site-form-item-icon" />} disabled placeholder="********" iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)} />
                  </Form.Item>

                  <Form.Item
                    name="totalStorageSize"
                    rules={[{ required: true, message: 'merci d\'ajouter l\'espace souhaitée !' }]}
                  >
                    <Input prefix={<i class="fas fa-cloud-upload-alt"></i>} suffix="Go" placeholder={client.totalStorageSize} disabled />
                  </Form.Item>

                  <Form.Item
                      name="fileSize"
                      rules={[{required: true, message: 'merci d\'ajouter la taille des fichiers souhaités !' }]}
                  >
                    <Input prefix={<i class="fas fa-cloud-upload-alt"></i>} suffix="Ko" placeholder={client.fileSize} disabled />
                  </Form.Item>
                  <Form.Item
                      name="extentionFile"
                      rules={[{ required: true, message: 'merci d\'ajouter les extentions souhaitée !' }]}
                  >

                    {displayListExtention.length!=0?<Select
                        mode="multiple"
                        disabled
                        style={{ width: "80%"}}
                        defaultValue={displayListExtention}
                        onChange={handleChange}
                    >
                      {displayListExtention}

                    </Select>:<></>}

                  </Form.Item>

                </div>) : (<div className="FormAppSubsc"
                  name="basic"
                  initialValues={{ remember: true }}
                  onFinish={onFinish}
                  onFinishFailed={onFinishFailed}
                >
                  <Form.Item
                    name="application"
                    rules={[{ required: true, message: 'merci d\'ajouter le nom de l\'application !' }]}
                  >
                    <Input prefix={<i className="far fa-building" ></i>}  placeholder={client.application?client.application:"Client Name" } />
                  </Form.Item>
                  <Form.Item
                    name="login"
                    rules={[{ required: true, message: 'merci d\'ajouter un login !' }]}
                  >
                    <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Login" />
                  </Form.Item>


                  <Form.Item
                    name="password"
                    rules={[{ required: true, message: 'merci d\'ajouter un mot de passe !' }]}
                  >
                    <Input.Password prefix={<LockOutlined className="site-form-item-icon" />} placeholder="Mot de passe" iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)} />
                  </Form.Item>

                  <Form.Item
                      name="totalStorageSize"
                      rules={[{ required: true, message: 'merci d\'ajouter l\'espace souhaitée !' }]}
                  >
                    <Input type="number" min="0" prefix={<i class="fas fa-cloud-upload-alt"></i>} suffix="Go" placeholder={client.totalStorageSize?client.totalStorageSize:"Espace souhaitée" }/>
                  </Form.Item>

                  <Form.Item
                      name="fileSize"
                      rules={[{ required: true, message: 'merci d\'ajouter la taille des fichiers souhaités !' }]}
                  >
                    <Input type="number" min="0" prefix={<i class="fas fa-cloud-upload-alt"></i>} suffix="Ko" placeholder={client.fileSize?client.fileSize:"Taille des fichiers" } />
                  </Form.Item>

                  <Form.Item
                      name="extentionFile"
                      rules={[{ required: true, message: 'merci d\'ajouter les extentions souhaitée !' }]}
                  >
                    <Select
                        mode="multiple"
                        allowClear
                        style={{ width: "80%"}}
                        placeholder="Choisir les extentions des fichiers"
                        onChange={handleChange}
                    >
                      {extentionFile}
                    </Select>
                  </Form.Item>
                  <Form.Item shouldUpdate={true} >
                    <Button type="primary" htmlType="submit">
                      Valider
                    </Button>
                  </Form.Item>
                </div>)}
              </div>
            </Card>
          </Col>
          </Form>
        </Row>
      </Container>
    </>
  );
}

export default User
