import React, { useState, useContext } from 'react'
import { Form, Input, Button } from 'antd';
import { useHistory, NavLink } from "react-router-dom";
import { UserOutlined, LockOutlined, PoweroffOutlined, EyeInvisibleOutlined, EyeTwoTone } from '@ant-design/icons';
import logo from '../../../assets/img/logofinaln.png'
import './Login.css'
import Zoom from 'react-reveal/Zoom';
import { saveAuthToken } from '../../../services/localStorage-service'
import AuthentificationContext from '../../../context/authentification-context'
import AdminService from '../../../services/admin-Service'
import Notification from '../../shared/notification/Notification'

export default function Login() {

    const authentificationContext = useContext(AuthentificationContext);
    const history = useHistory();
    const [form] = Form.useForm();
    const [loading, setLoading] = useState(false);


    const onFinish = values => {

        setLoading(true)
        let userCredential = {
            login: values.username,
            password: values.password
        }

        AdminService.authenticate(userCredential).then(resp => {

            saveAuthToken(resp.data)
            setLoading(false)
            history.push({ pathname: "/admin/user" })
            authentificationContext.logIn()
            Notification('success', 'Authentification réussie', 'Bienvenue à EDM')


        }

        ).catch(error => {

            let err = { ...error }

            if (err.response) {
               
                 Notification('error', "Erreur d'authentification", 'Merci de vérifier votre identifiant ou mot de passe !')             
                      setLoading(false)
                
            }

            else {
                Notification('error', "Erreur Serveur", 'Merci de réessayer plus tard')
                setLoading(false)
            }
        })

    }

    return (


        <Zoom >

            <Form form={form} name="normal_login" className="login-form" onFinish={onFinish}>
                <img className="logo" src={logo} alt=".logo" />
                <Form.Item
                    name="username"
                    rules={[{ required: true, message: 'Quel est votre login ?' }]}
                >
                    <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Login" />
                </Form.Item>
                <Form.Item
                    name="password"
                    rules={[{ required: true, message: 'Quel est votre mot de passe ?' }]}
                >
                    <Input.Password
                        prefix={<LockOutlined className="site-form-item-icon" />}
                        type="password"
                        placeholder="Mot de passe"
                        iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                    />
                </Form.Item>
                <div className="login-register-container">
                    <Form.Item shouldUpdate={true} className="login_submit_button">
                        {() => (
                            <Button
                                type="primary"
                                htmlType="submit"
                                icon={<PoweroffOutlined />}
                                loading={loading}
                            >
                                Se Connecter
                            </Button>
                        )}
                    </Form.Item>
                    <span className="register">  <NavLink to="/register"  > Pas encore un client ? Inscrivez-vous  </NavLink> </span>
                </div>
            </Form>
        </Zoom>

    )
}
